import React, { useEffect, useState } from "react";
import { Card, CardBody, CardHeader, Col, Row, Table } from "reactstrap";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";

const User = (props) => {
  const { id } = props.match.params;
  const [user, setUser] = useState([]);
  if (!id) {
    props.history.push("/dashboard");
  }

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query alluser($userId:ID,$page:Int,$limit:Int){
          getUsers(userId:$userId,page:$page,limit:$limit){
            _id,
            phone,
            fname,
            address,
            email,
          }
        }
        `,
        variables: {
          userId: id,
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          toast.error("خطا در دریافت اطلاعات");
        } else {
          setUser(res.data.data.getUsers[0]);
        }
      })
      .catch((err) => console.log(err));
  }, [id]);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col lg={12}>
          <Card>
            <CardHeader>
              <strong>
                <i className="icon-info pr-1"></i>ایدی کاربر:{" "}
                {props.match.params.id}
              </strong>
            </CardHeader>
            <CardBody>
              <Row className="my-5">
                <Col>نام : {user.fname}</Col>
                <Col>شماره همراه :{user.phone}</Col>
                <Col>نام خانوادگی : {user.lname? user.lname : '*******'}</Col>
                <Col>شماره ثابت :{user.number ? user.number : '******'}</Col>
              </Row>
              <Row>
                <Col>آدرس : {user.address}</Col>
                <Col>ایمیل :{user.email ? user.email : '******'}</Col>
                <Col>تاریخ تولد : {user.date? user.date : '*******'}</Col>
                <Col>کد پستی :{user.code ? user.code : '******'}</Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default User;
